<template>
  <!-- <div></div> -->
  <div>
    <div>
      <!--  -->
      <van-nav-bar
        class="page-nav-bar"
        left-arrow
        title=""
        @click-left="$router.push({ path: '/my' })"
      ></van-nav-bar>
      <!-- / -->
    </div>
    <div class="celldiv">
      <van-cell center title="" is-link>
        <template>
          <van-image round class="userphoto" :src="userinfo.photo" />
        </template>
      </van-cell>
      <!--  -->
      <input
        type="file"
        ref="userphoto"
        class="userphotoimg"
        @change="optionImg"
      />
      <van-cell
        title=""
        :value="userinfo.name"
        is-link
        @click="editNameShowFn"
      />
      <van-cell
        title=""
        :value="userinfo.gender == 0 ? '' : ''"
        is-link
        @click="editGenderShowFn"
      />
      <van-cell
        title=""
        @click="editBirthdayShowFn"
        :value="userinfo.birthday"
        is-link
      />
    </div>
    <!--  -->
    <van-popup
      class="vanpopup"
      v-model="editNameShow"
      position="bottom"
      style="height: 100%"
    >
      <van-nav-bar
        title=""
        left-text=""
        right-text=""
        @click-left="editNameShow = false"
        @click-right="editNameFn"
      />
      <div class="popupname">
        <van-field
          v-model="nameMessage"
          rows="2"
          autosize
          type="textarea"
          maxlength="7"
          placeholder=""
          show-word-limit
        />
      </div>
    </van-popup>
    <!--  -->
    <van-popup class="vanpopup" v-model="editGenderShow" position="bottom">
      <van-picker
        title=""
        show-toolbar
        :columns="columns"
        :default-index="userinfo.gender"
        @confirm="editGenderFn"
        @cancel="editGenderShow = false"
      />
    </van-popup>

    <!--  -->
    <van-popup class="vanpopup" v-model="editBirthdayShow" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="editBirthdayFn"
      />
    </van-popup>

    <!--  -->
    <van-popup
      class="vanpopup"
      v-model="editPhotoShow"
      position="bottom"
      style="height: 100%"
    >
      <div class="imgbg" v-if="editPhotoShow">
        <!-- img -->
        <img
          ref="yulanPhotoDom"
          class="imgbgphoto"
          :src="yulanPhotoSrc"
          alt=""
        />
        <div class="toolbar">
          <div class="cancel" @click="editPhotoShow = false"></div>
          <div class="confirm" @click="onConfirmPhoto"></div>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import moment from "moment";
import "cropperjs/dist/cropper.css";
import Cropper from "cropperjs";
import {
  getUserProfileApi,
  updataUserProfileApi,
  edituserinfoPhotoApi,
} from "@/api/user";
export default {
  data() {
    return {
      editNameShow: false, //
      editGenderShow: false, //
      editBirthdayShow: false, //
      editPhotoShow: false, //
      nameMessage: "", //
      userinfo: {}, //
      columns: ["", ""],
      currentDate: new Date(), //
      minDate: new Date(1970, 0, 1), //
      maxDate: new Date(), //
      yulanPhotoSrc: "", //
      cropper: "", //cropper
    };
  },
  async created() {
    try {
      let { data } = await getUserProfileApi();
      console.log(data);
      this.userinfo = data.data;
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
    // 
    editNameShowFn() {
      this.editNameShow = true;
      this.nameMessage = this.userinfo.name;
    },
    //
    editGenderShowFn() {
      this.editGenderShow = true;
    },
    //
    editBirthdayShowFn() {
      this.editBirthdayShow = true;
    },

    //
    async editNameFn() {
      try {
        this.$toast.loading({
          message: "...",
        });
        let obj = {
          name: this.nameMessage,
        };
        await updataUserProfileApi(obj);
        this.$toast.success("");
        this.editNameShow = false; //
      } catch (error) {
        this.$toast.fail("");
        console.log(error);
      }
    },

    //
    // itemindex
    async editGenderFn(item, index) {
      try {
        this.$toast.loading({
          message: "...",
        });
        let obj = {
          gender: index,
        };
        await updataUserProfileApi(obj);
        this.$toast.success("");
        this.editGenderShow = false; //
        //
        //
        this.userinfo.gender = index;
      } catch (error) {
        this.$toast.fail("");
        console.log(error);
      }
    },
    //
    async editBirthdayFn() {
      let date = moment(this.currentDate).format("YYYY-MM-DD");
      try {
        this.$toast.loading({
          message: "...",
        });
        let obj = {
          birthday: date,
        };
        await updataUserProfileApi(obj);
        this.$toast.success("");
        this.editBirthdayShow = false; //
        //
        //
        this.userinfo.birthday = date;
      } catch (error) {
        this.$toast.fail("");
        console.log(error);
      }
    },
    //
    optionImg() {
      // fils
      let obj = this.$refs.userphoto.files[0];
      let imgsrc = window.URL.createObjectURL(obj);
      //src
      this.yulanPhotoSrc = imgsrc;
      console.log(imgsrc);
      this.editPhotoShow = true;

      // const image = document.getElementById("image");
      //
      //vuedom
      //$nextTick
      //vuedom
      this.$nextTick(() => {
        let image = this.$refs.yulanPhotoDom;
        console.log(image);
        this.cropper = new Cropper(image, {
          aspectRatio: 1, //
          viewMode: 1, //cropper
          dragMode: "move", //cropper//crop: 3move: 3
          // autoCropArea: 1,
          cropBoxMovable: false, //
          cropBoxResizable: false, //
          background: false, //
        });
      });
    },

    onConfirmPhoto() {
      //getCroppedCanvas
      //toBlob Blob  canvas 
      this.cropper.getCroppedCanvas().toBlob(async (blob) => {
        //Blob 
        const formData = new FormData();

        // Pass the image file name as the third parameter if necessary.
        formData.append("photo", blob /*, 'example.png' */);
        try {
          this.$toast.loading({
            message: "...",
          });
          let { data } = await edituserinfoPhotoApi(formData);
          this.$toast.success("");
          //
          this.userinfo.photo = data.data.photo;
          this.editPhotoShow = false;
        } catch (error) {
          this.$toast.fail("");
          console.log("" + error);
        }
      });
      console.log("");
    },
  },
};
</script>
<style lang="less" scoped>
.vanpopup {
  background-color: #f5f7f9;
  .popupname {
    padding: 20px;
  }
}
.userphoto {
  width: 60px;
  height: 60px;
}
.celldiv {
  position: relative;
  .userphotoimg {
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    opacity: 0;
    height: 100px;
  }
}
.imgbg {
  width: 100%;
  height: 100%;
  background: #000;
  .imgbgphoto {
    display: block;
    max-width: 100%;
  }
}
.toolbar {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  .cancel,
  .confirm {
    width: 90px;
    height: 90px;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
  }
}
</style>
