export const getUserProfileApi = () =>
  request({
    url: "/v1_0/user/profile",
    method: "get",
  });

//
export const updataUserProfileApi = (data) =>
  request({
    url: "/v1_0/user/profile",
    method: "patch",
    data,
  });

//
export const edituserinfoPhotoApi = (data) =>
  request({
    url: "/v1_0/user/photo",
    method: "patch",
    data,
  });
